// IMPORTS
// Material UI
import { Grid } from '@material-ui/core'
import Button from '@material-ui/core/Button'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/core/styles/withStyles'
import PersonAddIcon from '@material-ui/icons/PersonAdd'

// Externals imports (Dependencies)
import React, { Component, Fragment } from 'react'
import { connect } from 'react-redux'
import { Link, Route } from 'react-router-dom'
import { bindActionCreators } from 'redux'
import PropTypes from 'prop-types'

// Internal imports
import { fetchStores } from '../StoreActions'
import { getStoreListData } from '../StoreReducer'
import styles from '../../../resources/theme/stores'
import EditStore from './Edit'
import NewStore from './New'
import List from './List'

class Store extends Component {
  componentDidMount() {
    this.props.fetchStores()
  }

  render() {
    const { classes, items } = this.props
    console.log(items)

    return (
      <Fragment>
        <Grid container justify="center" alignItems="center">
          <Grid item className={classes.grow}>
            <Typography variant="h3">Lojistas</Typography>
          </Grid>
          <Grid item>
            <Button
              size="medium"
              color="primary"
              variant="contained"
              component={Link}
              to="/stores/new"
            >
              <PersonAddIcon className={classes.btnIcon} /> Cadastrar
            </Button>
          </Grid>
        </Grid>

        <Route path="/stores/new" component={NewStore} />
        <Route path="/stores/:id/edit" component={EditStore} />

        <List classes={classes} items={items} />
      </Fragment>
    )
  }
}

Store.propTypes = {
  classes: PropTypes.object.isRequired,
  fetchStores: PropTypes.func.isRequired,
  items: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  ...getStoreListData(state),
})
 
const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      fetchStores,
    },
    dispatch
  )

// export Store class
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(Store))
